#titleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  gap: 30px;
}
#titleContainer h1 {
  background-color: rgb(25, 32, 54);
  padding: 10px;
  border-radius: 30px;
  color: rgb(121, 122, 138);
  font-size: 1.8rem;
  font-weight: 800;
}

#titleContainer button {
  font-size: 1rem;
  height: 40px;
  width: 150px;
  font-weight: 800;
}

.contentBlogs {
  width: 100%;
  height: 60vh;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 10px;
  align-items: center;
  justify-content: center;
}

.commentBlogs {
  border-radius: 30px;
  width: 200px;
  height: min-content;
  background-color: white;
  overflow-wrap: break-word;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  overflow: hidden;
  animation: opacity 3s;
  cursor: pointer;
  transition: 0.2s ease-in;
}
.commentBlogs:hover {
  transform: scale(1.1);
}
.commentName {
  font-size: 0.8rem;
  text-align: left;
}

.commentName p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 20px;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 1332px) {
  #titleContainer h1 {
    font-size: 1rem;
  }
  .contentBlogs {
    height: 100vh;
    overflow-y: scroll;
    flex-direction: column;
  }
}
