.LeftMainContainerMain {
  width: 60%;
  height: 80vh;

  border-radius: 30px;
  box-shadow: 1px 10px 200px rgba(87, 87, 119, 0.5);
  overflow: hidden;
  animation: toTheRight 3s;
}
.header {
  width: 100%;
  height: 80px;
  background-color: rgb(45, 44, 53);
  display: flex;
  align-items: center;
}

.mainContent {
  width: 100%;
  background: linear-gradient(
    rgb(131, 137, 148, 0.5),
    rgba(212, 221, 241, 0.5),
    rgb(185, 190, 211)
  );
  height: 71.9vh;
  display: flex;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.mainContent li {
  flex: 1 0 100%;
  list-style-type: none;
}

.elementContainer {
  display: none;
  transition: 0.5s ease-in;
}

.elementContainer.active {
  display: block;
  width: 100%;
  height: 30vh;
  animation: fadeIn 3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes toTheRight {
  from {
    transform: translateX(50%);
  }
  to {
    transform: translateX(0%);
  }
}
@media only screen and (max-width: 1332px) {
  .LeftMainContainerMain {
    width: 100%;
    height: 100vh;
    position: relative;
  }

  .mainContent {
    height: 100vh;
  }
  .elementContainer.active {
    height: 100vh;
  }
}
