.NavBarContainer {
  width: 100%;
}

.NavBarContainer ul {
  margin: auto;
  width: 90%;
  height: 40px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-between;
}

.navBars {
  color: white;
  width: 150px;
  font-size: 1rem;
  list-style: none;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.3s ease-in;
}
button {
  width: 200px;
  border-radius: 30px;
}
.navBars.active {
  transform: scale(1.2);
  font-weight: 800;
  color: rgb(3, 11, 56);
  background-color: rgb(160, 153, 153);
}

@media only screen and (max-width: 1332px) {
  .navBars {
    color: white;
    width: 150px;
    font-size: 0.8rem;
    list-style: none;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  .navBars.active {
    transform: scale(1);
    font-weight: 700;
    color: rgb(3, 11, 56);
    background-color: rgb(160, 153, 153);
  }
}
