.container {
  text-align: center;
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
  z-index: -1;
}

.RightSectionContainer h1 {
  margin: auto;
  text-align: left;
  width: 60%;
  font-size: 1.2rem;
  color: rgb(153, 148, 148);
}
.RightSectionContainer p {
  font-size: 0.8rem;
  font-weight: 700;
  color: rgb(45, 185, 178);
}
.RightSectionContainer .name h1 {
  font-size: 2rem;
  text-align: center;
  letter-spacing: 3px;
}
.RightSectionContainer {
  border-radius: 30px;
  width: 30%;
  height: 95vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    rgb(131, 137, 148, 0.6),
    rgba(23, 44, 138, 0.9),
    rgba(8, 17, 51, 0.9)
  );
  gap: 10px;
  justify-content: center;
}

.RightSectionContainer section {
  width: 100%;
}
.RightSectionContainer .image {
  width: 100%;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  object-position: center;
  border-radius: 30px;
}
.RightSectionContainer .icons {
  color: white;
}
.RightSectionContainer .contact {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.RightSectionContainer .contact ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.contact-list {
  margin: auto;
  width: 60%;
  justify-content: space-between;
  display: flex;
}
.contactLabel {
  width: 40%;
  display: flex;
  gap: 10px;
}

.user-container {
  width: 60%;
  text-align: left;
}

.onlineProfiles {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.onlineProfiles ul {
  margin: auto;
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.onlineProfiles ul li {
  list-style: none;
  width: 50%;
  text-align: left;
  display: flex;
  gap: 10px;
}
.onlineProfiles ul li a p {
  color: rgb(122, 96, 96);
  cursor: pointer;
}
.onlineProfiles ul li a {
  text-decoration: none;
}
.skills {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.skillsContainer {
  width: 60%;
  margin: auto;
}

.skillsContainer ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.skillsContainer ul li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 20px;
  gap: 10px;
}

.skillsContainer ul li span:nth-child(1) {
  width: 80%;
  text-align: left;
}
.levelContainer {
  width: 80%;
  height: 10px;
  background-color: rgb(63, 61, 61);
  border-radius: 30px;
}

.levelColor {
  background-color: rgb(98, 79, 168);
  border-radius: 30px;
}

@media only screen and (max-width: 1332px) {
  .RightSectionContainer {
    width: 100%;
    height: 100vh;
  }
}
