.MainContentContainer {
  width: 100%;
  height: 72vh;
  padding: 30px;
  gap: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.titleContainer {
  display: flex;
  align-items: left;
}

.titleContainer h1 {
  background-color: rgb(25, 32, 54);
  width: 200px;
  padding: 10px;
  border-radius: 30px;
  color: rgb(121, 122, 138);
  font-size: 1.5rem;
  font-weight: 800;
}

.contentParagraphContainer {
  width: 100%;
  height: 50vh;
  display: flex;
  align-items: center;
}
.contentParagraphContainer p {
  text-align: left;
  font-size: 1rem;
  width: 80%;
  margin: auto;
  overflow-wrap: break-word;
  font-weight: 700;
  color: rgb(23, 24, 26);
  animation: opacity 5s;
}
.content-title {
  font-size: 1.6rem;
}

.contentParagraphContainer p span {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 3px;
  color: rgb(5, 59, 63);
}

.contentParagraphContainer p span a {
  font-size: 1rem;
  text-decoration: none;
  color: rgb(5, 59, 63);
}

/* About  */

#AboutContent {
  display: flex;
  flex-direction: column;

  gap: 10px;
  height: 50vh;
}

#AboutContent ul {
  display: flex;
}

#AboutContent ul li {
  display: none;
}
#AboutContent ul li.active {
  flex: 1 0 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  animation: opacity 5s;
}

#AboutContent ul li.active .content-title {
  font-size: 1.6rem;
  font-weight: 800;
  color: white;
}

#AboutContent ul li.active p {
  height: 20vh;
}
#pageCount {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
#dotsLi {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: rgb(56, 55, 66);
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
#dotsLi.active {
  transform: scale(1.2);
  background-color: rgb(13, 8, 80);
}
/* Work History */
#work-history {
  width: 300px;
}

#workHistoryContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#workHistoryContainer .ImageCards {
  width: 200px;
  height: 250px;
  display: flex;
  flex-direction: column;
  background-color: rgba(1, 1, 27, 0.6);
  border-radius: 30px;
  overflow: hidden;
  position: relative;
  transition: 0.3s ease-in;
  animation: toTheRight 3s;
  cursor: pointer;
}
#workHistoryContainer .ImageCards:hover {
  transform: scale(1.1);
}
#workHistoryContainer .ImageCards::after {
  content: "Click Me";
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  inset: 0;
  position: absolute;
  background: linear-gradient(
    rgb(131, 137, 148, 0.6),
    rgba(30, 31, 36, 0.9),
    rgba(14, 16, 27, 0.9)
  );
  transition: 0.6s ease-in-out;
}
#workHistoryContainer .ImageCards:hover::after {
  transform: translateY(100%);
}

.imageHolder {
  height: 180px;
}
.imageHolder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#workHistoryContainer .ImageCards h1 {
  font-size: 1rem;
  color: rgb(198, 199, 214);
  padding: 0;
  margin: 0;
}
#workHistoryContainer .ImageCards p {
  width: 100%;
  text-align: center;
  font-size: 0.9rem;
  color: white;
  padding: 0;
  margin: 0;
}

/* Comment */

@keyframes toTheRight {
  from {
    transform: translateX(50%);
  }
  to {
    transform: translateX(0%);
  }
}

@media only screen and (max-width: 1332px) {
  .MainContentContainer {
    height: 100vh;
  }
  .titleContainer h1 {
    font-size: 1.2rem;
  }
  #AboutContent ul li.active {
    height: 50vh;
  }
  #AboutContent ul li.active .content-title {
    font-size: 1rem;
  }

  #workHistoryContainer {
    flex-direction: column;
    height: 70vh;
    gap: 20px;
  }
  #AboutContent ul li.active p {
    height: 40vh;
    font-size: 0.8rem;
    overflow-wrap: break-word;
  }
  #dotsLi {
    width: 20px;
    height: 20px;
  }
}
