.ContentPage {
  width: 100%;
  height: 100vh;
  background-color: rgb(7, 2, 27);
  gap: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1332px) {
  .ContentPage {
    flex-direction: column;
    overflow-y: scroll;
    height: auto;
  }
}
